<template>
  <div class="empty">
    <div class="empty-icon">
      <i class="icon icon-link"></i>
    </div>
    <p class="empty-title h5">App should be opened in a new Tab</p>
    <p class="empty-subtitle">Click the button to open manualy.</p>
    <div class="empty-action">
      <a class="btn btn-primary"
        :href = "this.params.url"
        target="_blank"
      >Open External App</a>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExternalApp',
  data () {
    return {
    }
  },
  props: {
    params: {}
  },
  methods: {
  },
  created: function () {
    var win = window.open('', '_blank')
    win.location.href = this.params.url
    win.focus()
  }
}
</script>

<style scoped>
.empty{
  height: 100%;
}

.empty-icon i{
  font-size: 2em;
  font-weight: bold;
  color: white;
  background-color: #1abc9c;
  padding: 30px;
  border-radius: 50%;
}
</style>



